export default {
    data() {
        return {
            turn_on: false,
            turn_on1: false,
            play: false,
            decorate: false,
            decorate1: false,
            fly: false,
            fly1: false,
            cake: false,
            cake1: false,
            candle: false,
            candle1: false,
            birthday: false,
            birthday1: false,
            bg_color: null
        }
    },
    methods: {
        // 开灯
        turnOn () {
            this.turn_on = true
            this.turn_on1 = true
            var that = this
            setTimeout(function() {
                that.play = true
            }, 3000)
        },
        // 播放音乐
        play_music () {
            this.play = false
            // 定义背景音频管理器
            const bgAudioMannager = uni.getBackgroundAudioManager()
            bgAudioMannager.title = '走过绿意';
            bgAudioMannager.singer = 'Kevin Kern';
            bgAudioMannager.coverImgUrl = 'http://i.htqyy.com/img7/0/149.jpg';
            bgAudioMannager.src = 'http://cdn.jie12366.xyz/play.mp3'
            bgAudioMannager.loop = true // 设置循环播放
            bgAudioMannager.play()
            this.decorate = true
            this.bg_color = '#fff'
            var that = this
            setTimeout(function() {
                that.decorate1 = true
            }, 3000)
        },
        // 开始装饰
        bannar_coming () {
            this.decorate1 = false
            this.fly = true
            var that = this
            setTimeout(function() {
                that.fly1 = true
            }, 3000)
        },
        // 放飞气球
        balloons_flying () {
            this.fly1 = false
            this.cake = true
            var that = this
            setTimeout(function() {
                that.cake1 = true
            }, 3000)
        },
        // 打开蛋糕
        cake_fadein () {
            this.cake1 = false
            this.candle = true
            var that = this
            setTimeout(function() {
                that.candle1 = true
            }, 3000)
        },
        // 点蜡烛
        light_candle () {
            this.candle1 = false
            this.birthday = true
            var that = this
            setTimeout(function() {
                that.birthday1 = true
            }, 3000)
        },
        // 想说的一些话
        wish_message () {
            
        }
    }
}
